import { Component } from '@angular/core';
import {  ActionSheetController  ,AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
@IonicPage()
@Component({
  selector: 'page-denuncia-seg',
  templateUrl: 'denuncia-seg.html',
})
export class DenunciaSegPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';
  v1:any;
  v2:any;
  v3:any;
  v4:any;
  idevento: any;
  nevento: any;
  ubica: any;
  fecha: any;
  tiposus: any=[];
  unidadt: string = "";
  idsuscriptor: any;

  public hasFilter: boolean = false;
  public gLimit: number = 500;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  idempresa:any;
  idafiliado:any='na';

  constructor( public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController,public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  popimg2(item: any, $event) {
    //http://54.226.181.255:8500/diplomaxx.cfm?nombre=%20mario%20roberto%20morales%20merida
   
    window.open("http://54.226.181.255:8500/diplomaxx.cfm?nombre=" + (item.nombre).toUpperCase() +' ' +(item.apellido).toUpperCase()  ,'_system', 'location=yes');
     }


     popimg(item: any, $event) {
      //let modal = this.modalCtrl.create(ModalContentPage, item.nombre);
     // modal.present();
     // console.log(item.estado);
     var data = { message : item._id };
     var modalPage2 = this.modalCtrl.create('QrviewPage',data);
     modalPage2.present();
    }
      

  pideregistros(op) { 
      let self = this;
      self.loading = true;
     
      this.regService.getReg(this.idsuscriptor + '/' + op + '/todos','participa33s').then((data) => {
 
      this.items=data;
      this.items2=data;
      self.loading = false;

    
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
  }
  ionViewDidLoad() {
    

    this.storage.get('tiposus').then((value) => {
      this.tiposus = value.split(',');
    this.unidadt=this.tiposus[7]
    this.idsuscriptor=this.tiposus[3]
   

    this.pideregistros('Asignado');

  
  });



    this.translateService.get(["CANCEL",
    "INGRESAR",
    "BUSCARPOR",
    "BUSCAR"
  ]).subscribe(
    (values) => {
     this.v1=values.CANCEL
     this.v2=values.INGRESAR
     this.v3=values.BUSCARPOR
     this.v4=values.BUSCAR
    });

  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 500;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'participas').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  
    
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('DenunciaSegcreatePage',  { record : item ,record3:this.navParams.data.record,record2:{idevento:this.idevento,nidevento:this.nevento}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      this.pideregistros('Asignado');
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
  
   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }

   excludeTracks: any = [];


    
  OnExport = function ()
  {
    this.regService.abrelinkinterno('datosfijos/excel-participa/'+  this.idevento);
    
  }
  showFilter()     
  {

    let self = this;
    self.loading = true;
   
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Filtrar por estado:',
    buttons: [
      {
        text: 'Participante registrado',
        handler: () => {
        
          this.items=this.items2
          this.items= this.items2.filter((v) => {
         
            if(v.estado ) {
              
              if (v.estado=='Creado') {
                return true;
              }
              return false;
              
            }
          });
          self.loading = false;
         
          this.hasFilter = true;
        }
      },
      {
        text: 'Participante registrado, correo enviado',
        handler: () => {
          this.items=this.items2
          this.items= this.items2.filter((v) => {
            if(v.estado ) {
              self.loading = false;
              if (v.estado=='Ingreso, mail enviado') {
                return true;
              }
              return false;
              
            }
          });   
        }
      },
      {
        text: 'Participante a ingresado ( diplomas a imprimir)',
        handler: () => {
          this.items=this.items2
          this.items= this.items2.filter((v) => {
            if(v.estado ) {
              self.loading = false;
              if (v.estado=='Ingreso') {
                return true;
              }
              return false;
              
            }
          });   
        }
      },
      {
        text: 'Diplomas Impresos',
        handler: () => {
          this.items=this.items2
          this.items= this.items2.filter((v) => {
            if(v.estado ) {
              self.loading = false;
              if (v.estado=='Impreso') {
                return true;
              }
              return false;
              
            }
          });
          this.hasFilter = true;
        }
      }
     
    ]
  });

  actionSheet.present();


  }


  updateSchedule() {
    // Close any open sliding items when the schedule updates
   /*
    this.scheduleList && this.scheduleList.closeSlidingItems();

    this.confData.getTimeline(this.dayIndex, this.queryText, this.excludeTracks, this.segment).subscribe((data: any) => {
      this.shownSessions = data.shownSessions;
      this.groups = data.groups;
    });
    */

}

}



